.pulse-loader {
  height: 15px;
  margin: 10px 0;
  position: relative;
  width: 15px;
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out; }
  .pulse-loader:before, .pulse-loader:after {
    -webkit-animation: pulse-loader infinite 2s;
            animation: pulse-loader infinite 2s;
    box-shadow: 0 0 5px #bc9a36;
    border-radius: 50%;
    border: 3px solid #bc9a36;
    content: '';
    display: block;
    height: 100%;
    left: -3px;
    opacity: 0;
    position: absolute;
    top: -3px;
    width: 100%; }
    @media (min-width: 1200px) {
      .pulse-loader:before, .pulse-loader:after {
        box-shadow: 0 0 5px #ffffff;
        border: 3px solid #ffffff; } }
  .pulse-loader:after {
    -webkit-animation-delay: 2s / 2;
            animation-delay: 2s / 2; }

@-webkit-keyframes pulse-loader {
  0% {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0); }
  60% {
    opacity: 1; }
  100% {
    opacity: 0;
    -webkit-transform: scale(1);
            transform: scale(1); } }

@keyframes pulse-loader {
